(function(){

'use strict';

angular.module('app.main.category')
.controller('CategoryController', CategoryController);

CategoryController.$inject = ['toastr','data', 'api'];

function CategoryController(toastr, data, api){
	var category = this;
	category.getCategory = getCategory
	category.addCategory = addCategory;


	category.categoriesList = undefined;

	category.getCategory();
	function getCategory(){
		data.post(api.getCategory, null, true)
		.then(function(response){
			console.log('getCategory ::: ',response);
			category.categoriesList = response.data.categories;
		})
		.catch()
	}

	function addCategory(categoryObj){
		categoryObj.dateOfCreation = categoryObj.dateOfCreation || new Date();
		categoryObj.subCategories = categoryObj.subCategories || null;

		data.post(api.addCategory, categoryObj, false)
		.then(function(response){
			toastr.success(JSON.stringify(response), 'success')
			category.getCategory();
		})
	}
}

})();
(function(){

'use strict';

angular.module('app.core')
.factory('data',apiService);

apiService.$inject = ['$http','Upload'];

function apiService($http, Upload){
	var data = {
		get: 	fnGet,
		post: 	fnPost,
		put: 	fnPut,
		delete: fnDelete,
		upload: fnUpload, 
	}

	return data;
	function fnGet(url, dataToSend, cache){
		dataToSend = dataToSend || {};
		cache = cache || false;
		return $http.get(url, {params: dataToSend}, {cache: cache});
	}

	function fnPost(url, dataToSend, cache){
		dataToSend = dataToSend || {};
		cache = cache || {};
		return $http.post(url, dataToSend, {cache: cache});
	}

	function fnPut(url, dataToSend, cache){
	}

	function fnDelete(url, dataToSend, cache){}

	function fnUpload(url, dataToSend){
		return Upload.upload({
			url: url,
			headers: {
				"Content-Type" : undefined
			},
			data: dataToSend
		});
	}
}



})();
